				this.deskripsi = 'Perusahaan ini tergolong paling tua di lingkungan BUMY. Sesuai dengan akta pendiriannya, perseroan ini bernama PT. Perusahaan Pengangkutan, Veem dan Ekspedisi Marabunta atau disingkat PT. Marabunta. Didirikan dengan Akta Nomor 46 tanggal 17  Januari 1956 yang dibuat Notaris Eliza Pondang, dan dimuat dalam Berita Negara RI Nomor 91 tanggal 13 Nopember 1956.<br><br>Mengacu kepada akta pendiriannya, PT Marabunta dalam kegiatan usahanya bergerak dalam bidang perusahaan pengangkutan (baik yang melalui jalur laut atau perairan lain maupun yang melalui darat), perdagangan (termasuk menyimpan dan mengurus barang-barang muatan kapal laut dalam tempat penimbunan, mengurus surat-surat yang bersangkutan (dokumen) guna pemasukan dan pengeluaran barang-barang tersebut, serta menimbang, mengukur dan menguji pembungkusan barang-barang muatan kapal laut dalam lingkungan pelabuhan dan semua pekerjaan yang bersangkutan dengan itu), serta usaha-usaha perwakilan, keagenan dari perusahaan-perusahaan pelayaran, baik di dalam maupun di luar negeri.<br><br>Dalam perjalanannya, PT Marabunta kemudian menjadi anak perusahaan PT Truba, dan sejak status PT Truba sebagai holding company dicabut, maka PT Marabunta merupakan badan usaha YKEP hingga sekarang. Kepemilikan saham YKEP di Marabunta sebesar 97,5%, dan selebihnya 2,5% merupakan saham milik PT. Truba.<br><br>Untuk menggerakkan roda perusahaan, PT. Marabunta berkantor di Jalan Taman Pulo Gebang Blok B RT. 08/RW. 13  Kelurahan Cakung Timur, Kecamatan Cakung Jakarta Timur. Saat ini Pengurus PT Marabunta yakni Bapak I Made Wijana, M.Si, (Han) sebagai Komisaris,70 dan Bapak Suyatno, S.IP., M.Si, sebagai Direktur Utama'
			}else if(q == 'PT. TRI USAHA BHAKTI'){
				this.namaPt	= q
				this.imgPt = require('@/assets/img/bumy/pt-tri-usaha bhakti.jpg')
				this.deskripsi = 'Pada tanggal 24 Oktober 1968, didirikan PT. Tri Usaha Bhakti dan selanjutnya disingkat PT Truba dengan Akta Nomor 40 tanggal 24 Maret 1968 yang dibuat Notaris Soeleman Ardjasasmita.72 Sebagai pendiri sekaligus pemegang saham adalah Bapak Umar Wirahadikusumah, Bapak Hartono Wirjodiprodjo, dan Bapak Sudarto. Untuk pertama kalinya, Komisaris dan Direksi Truba terdiri dari:<br><br>Pendirian PT Truba dimaksudkan untuk membantu meringankan beban prajurit yang hidup dalam kondisi pra sejahtera, termasuk personel TNI Angkatan Darat yang telah dan menjelang pensiun. Dalam kegiatan usahanya, Truba bergerak di bidang perdagangan umum, pertambangan, pelayanan (service), pengangkutan, perkebunan, perindustrian, percetakan dan penerbitan.<br><br>Tidak berselang lama setelah pendirian PT Truba, pemerintah mengeluarkan larangan bagi para pejabat untuk duduk dalam kepengurusan di perusahaan. TNI Angkatan Darat dalam hal ini diwakili  oleh Kasad Jenderal Umar Wirahadikusumah merespon larangan ini dengan membentuk Yayasan Kartika Eka Paksi pada tahun 1972. Selanjutnya, PT. Truba sebagai perusahaan induk atas beberapa unit usaha telah menghibahkan seluruh sahamnya kepada YKEP pada tahun 1973.73 Saham yang dimiliki YKEP  sekarang atas PT. Truba adalah  99,37%, dan selebihnya 0.63% merupakan saham perorangan atas nama Bapak A. Agung Gde Suardhana, S.IP., M.Sc.<br><br>Dalam perkembangannya, PT. Truba pada 16 September 2002 menjadi holding company terhadap unit-unit usaha yang berada di bawah YKEP. Anak perusahaan dan mitra usaha (perusahaan patungan) Truba yang berada di bawahnya meliputi 20 perusahaan. Diantaranya adalah  PT.  ACE Insurance,  PT.  Aerotopografia, PT.Bhakti WiraHusada, PT. Cilegon Fabricators, PT. Marabunta, PT. Kartika Utama, dan lain-lain. Status holding company  PT. Truba ini tidak berlangsung lama, karena sejak 30 Juni 2005 status holding company dicabut, dan selanjutnya PT Truba secara operasional melakukan usaha sendiri.<br><br>Seiring dengan perjalanan waktu, bidang usaha PT Truba di bidang pertambangan yang dilaksanakan oleh anak perusahaannya PT Tri Bintang Abadi, pada Agustus tahun 2015 dihentikan oleh Pemda Bangka Belitung, dikarenakan kerusakan lingkungan, perusahaan-perusahaan lain yang sejenis juga dilarang operasional. Sejak tahun 2015, untuk kelancaran operasional perusahaan, PT Truba memanfaatkan aset yang dimiliki dan baralih ke bidang usaha jasa,74  yang meliputi penyewaan stockpile batu bara di Cirebon, penyewaan dump truk dan excavator, serta penyewaan aset Rumah Perkantoran (Rukan) di Tebet Utara Jakarta Selatan.<br><br>Guna mencapai tujuan perusahaan, Truba menetapkan visi menjadi salah satu perusahaan swasta nasional yang besar, profesional dan menguntungkan, sehingga dapat memberikan kontribusi dalam pembangunan dan pemberdayaan masyarakat lokal<br><br>Mengalir dari visi yang ditetapkan, PT Truba mengembang misi meningkatkan kinerja perusahaan melalui peningkatan kemampuan secara berkala dan menyeluruh yang meliputi peningkatan skill sumber daya manusia, sarana dan prasarana kerja serta manajemen di seluruh bidang usaha yang dijalankan.<br><br>Untuk menjalankan roda perusahaan, kini Truba menempati salah satu ruang kantor YKEP di jalan Medan Merdeka Timur Nomor 7 Gambir, Jakarta Pusat. Perusahaan dikendalikan oleh dua orang pimpinan, yaitu Ir. Imam Sardjono bertindak sebagai Direktur,75 dan Drs. Jhonny Munir sebagai Direktur.'
			}else if(q == 'PT. BHAKTI WIRA HUSADA'){
				this.namaPt	= q
				this.imgPt = require('@/assets/img/bumy/pt-bhakti-wira-husada.jpg')
				this.deskripsi = 'Perusahaan ini didirikan pada tanggal 25 Juni 1969 melalui Akta Nomor 39 yang dibuat Notaris Soeleman Ardjasasmita.77 PT. Bhakti Wira Husada dan selanjutnya disingkat PT BWH didirikan atas prakarsa Panglima Angkatan Darat (sekarang Kasad) melalui Perintah Nomor: Prin-246/5/1969 tanggal 23 Mei 1969.  Sebagai pendiri sekaligus pemegang sahamnya adalah dr. Soejoto dan dr. Ambjah Hadiwinoto.<br><br>Pada awalnya kegiatan usaha PT BWH bergerak dalam bidang perindustrian terutama industri pharmasi maupun alat-alat atau barang-barang perlengkapan kesehatan, apotik, laboratorium, serta menjalankan usaha-usaha dan bertindak sebagai perwakilan atau peragenan dari perusahaan-perusahaan lain, baik di dalam maupun di luar negeri. Untuk pertama kalinya, Komisaris dan Direksi BWH terdiri  dari:<br><br>Dalam perkembangannya, BWH kemudian menjadi anak perusahaan PT. Tri Usaha Bhakti. Seiring dengan dicabutnya PT Truba dari statusnya sebagai holding company sejak 30 Juni 2005, maka PT BWH menjadi badan usaha yang bernaung di bawah YKEP hingga sekarang, dengan kepemilikan saham Yayasan sebesar 99%, dan selebihnya 1% merupakan saham perorangan atas nama Bapak Bambang Sugiharto.<br><br>Dalam menjalankan roda perusahaan, kegiatan usaha PT BWH meliputi perdagangan, industri, pembangunan, dan bidang jasa, kedudukan perusahaan di Jalan Tebet Utara I Nomor 20 Jakarta Selatan, dengan susunan Pengurus: Drs. Abdul Gaffar Djaelani, Apt. sebagai Komisaris,78 Drs. Afifi Yusuf, Apt., sebagai Direktur Utama, dan Drh. Djoko Waluyo Budi Rahardjo, M.Sc. sebagai Direktur.'
			}else if(q == 'PT. WAHANA BHAKTI UTAMA'){
				this.namaPt	= q
				this.imgPt = require('@/assets/img/bumy/pt-wahana-bhakti-utama.png')
				this.deskripsi = 'Pada tanggal 20 September 1969 didirikan PT. Wahana Bhakti Utama dan selanjutnya disingkat WBU melalui Akta Nomor 08 yang dibuat Notaris Soeleman Ardjasasmita. PT. WBU merupakan anak perusahaan PT Truba.<br><br>Dalam kegiatan usahanya, PT. WBU bergerak dalam bidang pergudangan, pembangunan, perdagangan, dan jasa. Untuk menggerakkan perusahaan, PT. WBU berkantor di Jalan Dr. Saharjo Nomor 45 Kelurahan Manggarai, Kecamatan Tebet, Jakarta Selatan.<br><br>Dalam perjalanannya, PT. WBU pada tahun 1984 dihibahkan kepada YKEP, dan hingga sekarang ini berstatus sebagai badan usaha YKEP. Sebagai badan usaha, YKEP memegang saham terbesar dengan kepemilikan 99 % dan selebihnya 1 % merupakan saham perorangan atas nama Ir. Argunadi. Guna mengendalikan operasional perusahaan, PT WBU dipimpin oleh Bapak Herry AS sebagai Komisaris, dan Bapak Rulady Soedjono Dorestia sebagai Direktur.'
			}else if(q == 'PT. SAFIRA'){
				this.namaPt	= q
				this.imgPt = require('@/assets/img/bumy/pt-safira.jpg')
				this.deskripsi = 'Perusahaan ini didirikan pada tanggal 30 Juni 2005 melalui Akta Nomor 14 yang dibuat oleh Notaris Zainudin Ahmadi, S.H.80 Sesuai dengan akta pendiriannya, PT. Safira bergerak dalam bidang usaha perkebunan (karet, kelapa, dan coklat). Lokasi perkebunan yang menjadi lahan PT. Safira berada di Jl. Raya Palabuhan Ratu Km.129 Kp. Cirangkasbitung Desa Mangunjaya, Kec. Bantargadung, Kab. Sukabumi, Jawa Barat.<br><br>PT. Safira merupakan badan usaha  YKEP dengan kepemilikan saham sebesar 99 %, dan selebihnya 1 % merupakan saham perorangan atas nama Bapak Bambang Sujarwo. Dalam menggerakkan roda perusahaan, PT Safira saat ini dipimpin oleh bapak Bambang Sugiharto sebagai Komisaris, dan bapak Gunawan Hidayat, S.IP sebagai Direktur Utama.'
			}else if(q == 'PT. SAHARA MULTI HIJAU'){
				this.namaPt	= q
				this.imgPt = require('@/assets/img/bumy/sahara-multi-hijau.jpg')
				this.deskripsi = 'Pada tanggal 25 Januari 2010 didirikan  PT. Sahara Multi Hijau dan selanjutnya disingkat PT SMH   dengan Akta Nomor 16 yang dibuat Notaris Sjaaf De Carya Siregar,SH.81 Sebagai pendiri sekaligus pemegang sahamnya adalah Ibu Zahara Zaqiah dengan kepemilikan saham sebesar 90% dan Drs. Mohamad  Gofar sebesar 10%.  Dalam  kegiatan  usahanya,  PT.  SMH bergerak dalam bidang pembangunan, perdagangan, pertambangan dan pertanian.<br><br>Dalam perjalanannya kepemilikan saham Ibu Zahara Zaqiah dialihkan ke YKEP sebesar 70% dan saham Drs. Mohamad Gofar seluruhnya dialihkan ke YKEP. Dengan demikian kepemilikan saham Yayasan di PT SMH sebesar 80 %, dan saham Ibu Zahara Zaqiah sebesar 20%. Guna menggerakkan roda perusahaan, PT. SMH saat ini berkantor di Jakarta Selatan dan menjalankan usahanya di Sadang Purwakarta.'
			}else if(q == 'PT. CAKRAWALA BANGUN PERSADA'){
				this.namaPt	= q
				this.imgPt = require('@/assets/img/bumy/pt-cakrawala-bangun-persada.jpg')
				this.deskripsi = 'Perusahaan ini didirikan pada tanggal 21 Oktober 2011 melalui Akta Notaris Mira Oktaria, SH., M.Kn. Sebagai pendiri dari PT. Cakrawala Bangun Persada dan selanjutnya disingkat PT CBP sekaligus pemegang saham adalah Bapak Agus Laiman, Bapak Junaedi, Bapak Daniel, SE., dan Bapak Kurniadi, Bsc.<br><br>Sesuai dengan akta pendiriannya, PT CBP dalam kegiatan usahanya bergerak dalam bidang jasa dan konsultasi, perdagangan, pembangunan, industri, perbengkelan, pertanian, percetakan, dan bidang transportasi.<br><br>Untuk menggerakkan roda perusahaan, CBP berkantor di Jalan Achmad Yani Kp. Babagan Bogor RT.002/RW.006 Desa Dawuan Barat, Kecamatan Cikampek Kabupaten Karawang, dengan kegiatan usaha sekarang ini bergerak di bidang industri (semen mortar), perdagangan dan pembangunan. Sejak April 2015 seluruh kepemilikan saham para pendiri PT CBP dialihkan ke YKEP, dengan demikian PT CBP merupakan badan usaha YKEP dengan kepemilikan saham Yayasan sebesar 98%, dan selebihnya sebesar 2% merupakan saham perorangan atas nama Bapak Syaiful Ilyas. Pada saat ini Pengurus PT CBP dipimpin oleh Bapak Ir. Sugeng Susanto, M.M'
			}else if(q == 'PT. GRAHA KARTIKA ANUGERAH'){
				this.namaPt	= q
				this.imgPt = require('@/assets/img/bumy/pt-graha-kartika-anugerah.jpg')
				this.deskripsi = '"Sejarah berulang.Ungkapan ini kiranya tepat untuk menggambarkan pendirian PT. Graha Kartika Anugerah dan selanjutnya disingkat  PT GKA. Sejarah berulang bukan berarti peristiwanya yang berulang, akan tetapi nuansa dalam peristiwa itu ada kemiripan.<br><br>Seperti telah diuraikan pada awal buku ini, bahwa Yayasan Kartika Eka Paksi didirikan pada tanggal 10 Agustus 1972 oleh Kasad Jenderal Umar Wirahadikusumah, dan selanjutnya bertindak sebagai Ketua Dewan Pengurus YKEP. 43 (empat puluh tiga) tahun kemudian tepatnya pada tanggal dan bulan yang sama, yaitu 10 Agustus 2015, Ketua Pengurus YKEP Mayjen TNI (Purn) Adi Mulyono mendirikan badan usaha YKEP  yang diberi nama PT. Graha Kartika Anugerah. Bapak Adi Mulyono beserta segenap Pengurus YKEP, yang terdiri dari Bapak Aziz Ahmadi (Waket YKEP), Bapak Sulistijono (Sekretaris Pengurus YKEP) dan Bapak Sumartoyo (Bendahara Pengurus YKEP) secara bersama-samamendirikan PT. GKA melalui AktaNomor 04 tanggal 10 Agustus 2015 yang dibuat  Notaris Dr. Purbandari, S.H.,M. Hum., MM., M.Kn.<br><br>Sesuai dengan akta pendiriannya, PT. GKA bergerak dalam bidang pariwisata, dengan melaksanakan kegiatan usaha meliputi: biro  perjalanan  wisata, agen perjalananwisata,jasa impresariat, jasa konsultasi pariwisata, penyediaan akomodasi (hotel,pondok wisata, bumi perkemahan, persinggahan caravan), dan jasa pengelolaan properti (apartemen/kondominium, dan perkantoran), maupun bidang konvensi, perjalanan, dan pameran.<br><br>Dalam menjalankan roda perusahaan, PT. GKA berkantor di Gedung Artha Graha Lt. 26 Jalan Jenderal Sudirman Kav. 52-53 Jakarta Selatan dan kegiatan usaha di Jalan Kyai Tapa 101, Grogol Jakarta Barat. Untuk pertama kali Pengurus perusahaan terdiri dari:<br><br>Sebagai badan usaha, YKEP merupakan pemegang saham terbesar dari PT. GKA dengan kepemilikan saham sebesar    95 % dan selebihnya 5 % merupakan saham perorangan, yang saat ini atas nama Bapak A. Agung Gde Suardhana, S.IP., M.Sc.<br><br>Pada 10 Mei 2017 PT GKA melaksanakan Kerjasama Pemanfaatan (KSP) dengan Direktorat Zeni TNI Angkatan Darat (Ditziad) Nomor PK/003/V/2017-001/GKA/V/2017 atas tanah Kemhan/TNI Cq TNI Angkatan Darat di Jalan Kyai Tapa 101, Grogol Jakarta Barat, seluas 11.770 m2.<br><br>Dengan KSP tersebut, selama 2 (dua) tahun lebih PT GKA membangun Kartika Towers, yang terdiri dari 2 (dua) tower. Tower  untuk Hotel dan Convention Center dengan bangunan gedung 22 lantai, dengan fasilitas 189 kamar hotel, Convention Center (ball room) dengan kapasitas 2.000 orang, 8 meeting room, food & baverage. Sedangkan tower lainnya untuk perkantoran ritel.<br><br>Dalam menjalankan kegiatan usahanya, PT. GKA bekerjasama dengan PT Archipelago International Indonesia untuk pengelolaan Hotel Aston Kartika Grogol & Convention Center, sedangkan untuk pengelolaan perkantoran dan Ritel bekerja sama dengan PT Colliers International Indonesia.<br><br>Guna mencapai tujuan perusahaan, PT. GKA menetapkan visi mengelola pelayanan kinerja yang profesional untuk pengunjung dan penghuni kawasan Wisma Kartika Towers. Mengalir dari visi yang ditetapkan, PT. GKA mengemban misi memberikan kepuasan dan kenyamanan bagi pengunjung dan penghuni Wisma Kartika Towers, melalui budaya service exelent dan dengan inisiatif yang tinggi<br><br>Mengacu kepada Akta Nomor 01 tanggal 06 April 2017, dibuat oleh Notaris Dr. Purbandari, SH., M.Hum., M.Kn, susunan Pengurus perusahaan adalah Bapak Wiryantoro NK. 88, selaku Komisaris, Bapak Mochamad Anwar, selaku Direktur Utama, dan Bapak Ramli, S.H., S.E., selaku Direktur Keuangan.<br><br>Susunan Pengurus saat ini, terdiri dari Bapak Wiryantoro NK., selaku Komisaris, Bapak A. Agung Gde Suardhana, S.IP., M.Sc., selaku Direktur Utama, Bapak Ramli, SH., SE., selaku Direktur Keuangan, dan Bapak H. Heru Prijanto, S.IP., MBA., M.Par., selaku Direktur Operasional.'
			}else if(q == 'PT. NIRMALA ANUGERAH SEJAHTERA'){
				this.namaPt	= q
				this.imgPt = require('@/assets/img/bumy/pt-nirmala-anugerah-sejahtera.jpg'),
				this.deskripsi = 'Tidak dapat dipungkiri  bahwa setiap tahun performa pariwisata di Indonesia terus menanjak. Sektor ini semakin tumbuh dan bergerak maju, bahkan mampu meng-hadirkan prospek menjanjikan dan berdampak positif terhadap aktivitas perekonomian.<br><br>Melihat peluang tersebut, Mayjen TNI (Purn) Adi Mulyono sebagai Ketua Pengurus YKEP merasa perlu memanfaatkan lahan yang dimiliki Yayasan guna menghadirkan destinasi wisata yang nyaman, aman, ramah, menarik, dan mendidik. Untuk maksud tersebut dipilihlah daerah Sadang, Purwakarta-Jawa Barat.<br><br>Sebagai realisasinya, Bapak Adi Mulyono menguasakan kepada Sekretaris YKEP Drs. Sulistijono, M.M. dan pak Aris Setiabudi agar mengurus pendirian sebuah perseroan terbatas yang kegiatan usahanya bergerak di bidang Jasa Pariwisata. Perseroan Terbatas ini kemudian diberi nama PT. Nirmala Anugerah Sejahtera, dan selanjutnya disingkat PT. NAS.<br><br>PT. NAS didirikan melalui Akta Nomor 270 tanggal 30 Desember 2016yang dibuat Notaris Octaria Kuntjahyani, SH., M.Kn, Notaris&PPAT di Purwakarta.91 Sesuai dengan akta pendiriannya, PT. NAS bergerak dalam bidang usaha  wisata alam,  jasa  impresariat  (jasa  pariwisata), penyelenggaraanpertunjukan, jasa makanan dan minuman, penyediaan akomodasi, penyelenggaraan kegiatan hiburan dan rekreasi, serta usaha wisata air.<br><br>Untuk menggerakkan roda perusahaan, PT. NAS berkantor di Jalan Ipik Gandamanah RT. 001/RW.005 Ciseureuh, Kecamatan Purwakarta, Kabupaten Purwakarta, dengan Pengurus pada saat pendirian yakni Bapak Mochamad Anwar sebagai Komisaris dan Bapak Aris Setiabudi sebagai Direktur.<br><br>PT. NAS merupakan badan usaha YKEP dengan kepemilikan saham Yayasan sebesar 95%, dan selebihnya 5% merupakan saham perorangan atas nama Bapak Aris Setiabudi.<br><br>Saat ini PT. NAS mengelola wahana wisata di Purwakarta dengan nama Green Valley Water Park. Sesuai dengan nama dan design wahana wisatanya, Green Valley Water Park dilengkapi identitas sebagai ciri khas yang membedakannya dengan wahana wisata di tempat lain. Identitas itu dapat kita jumpai pada logo yang melekat pada tempat wisata ini.<br><br>Logo/lambang Green Valley Water Park tersebut berbentukperisaiyang mengandung arti sebagai penjagaan diri atau pelindung diri. Hal ini bermakna mengutamakan keamanan. Sedangkan lukisan berupa ornamen pesona alam mengandung arti tempat rekreasi, dan orang berkuda di depan tenda mengandung arti tempat out bound yaitu; bentuk pembelajaran yang dilakukan di alam terbuka atau tertutup dengan bentuk permainan yang efektif dengan menggabungkan antara intelegensia, fisik dan mental.Mengenai tulisanGreen Valley  Water Park telah menunjukkan bahwa tempat tersebut merupakan lembah  hijau dengan area bermain airyang berada di Sadang Purwakarta sebagai tempat petualangan untuk mendapatkan kegembiraan (Excitement and Adventure).Adapuntata warna yang menghiasi logo terdiri dari beberapa warna, yaitu: Hijau melambangkan kesegaran, kebugaran. Coklat:keakraban, Putih:kebebasan, Kuning: keceriaan, dan Biru melambangkan rasa percaya diri.<br><br>Arti dan makna logoGreen Valley  Water Park adalah taman lembahhijau dengan area bermain air di Sadang Purwakarta  sebagai wahana rekreasi, petualangan, dan outbound yang mengutamakan keamanan untuk menuju terwujudnya kesegaran atau kebugaran badan, keakraban, kebebasan, keceriaan, dan rasa percaya diri.<br><br>Wahana wisata ini menggunakan lahan seluas 33.840 m2, dengan menyediakan berbagai fasilitas wisata utamanya wisata air, yangmeliputi: kolam anak (kids pool), kolam Balita (tots pool), kolam renang (semi olympic pool), kolam arus (lazy river), spiral slides, race (body) slides, hujan salju, sepeda udara (sky cycling), kora-kora, digital planetarium, 5 D cinema, ATV (all terrain vehicle, kendaraan roda empat yang bisa dipakai di tengah medan apa saja layaknya motor trail), dan fish spa. Selain itu tersedia juga rumah hidro ponik maupun museum transportasi dan beberapa alutsista (alat utama sistem pertahanan).<br><br>Satu-satunya wahana wisata di Jawa Barat PT NAS bekerja sama dengan PT Kriyo Indo Makmur, menyediakan fasilitas planetarium hanyalah di Green Valley Water Park. Planetarium di water park ini merupakan generasi ke tiga di Indonesia,setelahTaman Ismail Marzuki dan Skyworld Taman Mini Indonesia Indah. Melalui planetarium ini, Green Valley  Water Parkmenyuguhkan media pendidikan bagi para pengunjungdengan memperagakan simulasi susunan bintang dan benda-benda langit, utamanya pengenalan berbagai macam planet angkasa, seperti: Merkurius,Venus, Bumi, Mars, Jupiter, Saturnus, Uranus,dan Neptunus.<br><br>Dalam perkembangannya, PT. NAS sebagai pengelola Green Valley  Water Parktelah mengalami pergantian pimpinan. Saat iniPengurus Perseroan dipimpin olehBapak Bambang Sugiharto sebagai Komisaris92 dan Bapak Hadi Kusnan, S.Pd.I., sebagai Direktur.'
